@media (max-width: 600px) {
  .rainbow-time-picker_modal {
    border-radius: 0 0 0.975rem 0.975rem;
    align-self: flex-start;
    width: 100%;
    max-width: 100%; }
  .rainbow-time-picker_time-select-content {
    width: 100%;
    margin: 56px 0 40px 0; }
  .rainbow-time-picker_time-select-value {
    width: 24%;
    font-size: 32px;
    font-weight: 100; }
    .rainbow-time-picker_time-select-value::placeholder {
      font-size: 34px;
      font-weight: 100; }
  .rainbow-time-picker_select-ampm {
    width: 24%;
    font-size: 32px; }
  .rainbow-time-picker_dots {
    margin: 0; }
  .rainbow-time-picker_icon-button-container {
    margin-left: 0; } }

@media (max-width: 340px) {
  .rainbow-time-picker_time-select-value {
    font-size: 24px; }
    .rainbow-time-picker_time-select-value::placeholder {
      font-size: 24px; }
  .rainbow-time-picker_select-ampm, .rainbow-time-picker_select-option {
    font-size: 22px; } }
